#nav-bar {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: space-around;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--darkgray);
}

.nav {
  font-size: 20px;
  margin: 10px 20px;
  color: var(--lightgray);
}

.nav:hover {
  color: var(--blue);
}

.calculator{
  width: 300px;
  box-shadow: 5px 5px 5px gray;
}

.display-container{
  background-color: var(--darkgray);
  color: var(--white);
  text-align: right;
}

.display {
  overflow: hidden;
  padding: 5px;
  height: 50%;
}

.value {
  font-size: 1.8rem;
}

.buttons-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 5px;
  padding: 5px;
  background-color: var(--gray);
}

.button {
  height: 70px;
  border: none;
  font-size: 1.3rem;
  border-radius: 10px;
}

.button:hover,
.equal:hover {
  box-shadow: 0px 0px 5px white;
}

.large-button {
  grid-column: 1 / 3;
}

.reset {
  background-color: #99c2ff;
}

.operator {
  color: var(--blue);
  background-color: var(--darkgray);
}

.number {
  color: var(--white);
  background-color: var(--lightgray);
}

.equal {
  color: var(--white);
  background-color: var(--blue);
  border: none;
  border-radius: 10px;
  grid-row: 4/ 6;
  grid-column: 4/ 5;
}

.converter-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: var(--gray);
  color: var(--white);
  text-align: center;
  padding: 20px;
  width: 350px;
  box-shadow: 5px 5px 5px gray;
}

.currency {
  font-family: inherit;
  padding: 10px;
  background-color: var(--darkgray);
  color: var(--white);
}

.symbol {
  display: inline-block;
}

.sign {
  font-size: 3rem;
  margin: 10px;
}

